                            texto: '<p>Trata-se de recurso especial interposto pelo Instituto Nacional do Seguro Social (INSS) em face da deciso da ${junta_de_recursos_contestada} Junta de Recursos.</p><p>A Autarquia Previdenciria recebeu o processo em ${data_do_recebimento_do_processo}, e a interposio do presente Recurso Especial ocorreu em ${data_do_protocolo_do_recurso_especial}.</p>',
