                            categoria: 'voto',
                            texto: '<p>No assiste razo ao INSS.</p><p><strong>ADMISSIBILIDADE</strong></p><p>O presente recurso  intempestivo em razo da Autarquia Previdenciria receber o processo em ${data_do_recebimento_do_processo}, data do incio do seu prazo, e somente interpor o presente Recurso Especial em ${data_do_protocolo_do_recurso_especial}, portanto, aps o prazo fixado pelo Art. 61 da Portaria MTP n 4.062/2022, que aprova o Regimento Interno do CRPS (RICRPS).&nbsp;</p><p>Somente  possvel propor relevao de intempestividade quando configurado o direito inequvoco da parte recorrente. Isso significa que, o reconhecimento do direito, no depende nem mesmo de baixar os autos em diligncia.</p><p>Na busca pela definio sobre liquidez e certeza do direito da parte, encontramos o conceito defendido por HELY LOPES MEIRELES, o qual se enquadra ao nosso preceito administrativo, de que: <i>" o que se apresenta manifesto na sua existncia, delimitado na sua extenso e apto a ser exercitado no momento da impetrao. Por outras palavras, o direito invocado, para ser amparvel por mandado de segurana, h de vir expresso em norma legal e trazer em si todos os requisitos e condies de sua aplicao ao impetrante: se a sua extenso ainda no estiver delimitada<strong>; se o seu exerccio depender de situaes e fatos ainda no indeterminados, no rende ensejo  segurana</strong>, embora possa ser defendido por outros meios judiciais"</i>. (obra: Mandado de Segurana  Ao Popular, Ao Civil Pblica, Mandado de Injuno, "Habeas Data")  Grifo Nosso.</p><p>Ainda menciona o Supremo Tribunal Federal: <i>A noo de direito lquido e certo ajusta-se, em seu especfico sentido jurdico-processual, ao conceito de situao decorrente de fato incontestvel e inequvoco, suscetvel de imediata demonstrao mediante prova literal pr-constituda. Precedentes (MS 26.552- AgR-AgR, Rel. Min. Celso de Mello, Plenrio, DJe 16.10.2009).</i></p><p>No caso em tela, inexiste inequvoca liquidez e certeza do direito da parte que justifique a relevao da intempestividade preceituada no 1 do Art. 57 do RICRPS.</p><p><strong>CONCLUSO</strong></p><p>Ante o exposto, voto por NO CONHECER DO RECURSO DO INSS, nos termos da fundamentao.</p>',
