                            texto: '<p>&nbsp;</p><p><strong>MRITO</strong></p><p>A Constituio da Repblica de 1988, no seu art. 201,  1, inciso I, ressalva a adoo de critrios diferenciados para a concesso de aposentadoria ao trabalhador sujeito, em seu labor, a efetiva exposio a agentes qumicos, fsicos e biolgicos prejudiciais  sade, ou associao desses agentes, vedada a caracterizao por categoria profissional ou ocupao.</p><p>Quanto ao reconhecimento da atividade exercida como especial, deve ser observado o tempo de servio disciplinado pela lei em vigor  poca em que efetivamente exercido, passando a integrar, como direito adquirido, o patrimnio jurdico do trabalhador. Desse modo, uma vez prestado o servio sob a gide de legislao, o segurado adquire o direito  contagem como tal, bem como  comprovao das condies de trabalho na forma ento exigida. Esta exegese  fundada no  6 ao artigo 188-P do Decreto n 3.048/99.</p><p>Para fins de enquadramento das categorias profissionais, devem ser considerados os Decretos ns 53.831/64 e 83.080/79 at 28/04/1995, data da extino do reconhecimento da atividade especial por presuno legal. Para o enquadramento dos agentes nocivos, devem ser considerados os Decretos ns 53.831/64 e 83.080/79 at 05/03/1997, o Decreto n 2.172/97 aps 06/03/1997 e o Decreto n 3.048/99 a contar de 07/05/1999.</p><p>H tambm a necessidade de haver a carncia mnima de 180 contribuies para se ter direito ao benefcio, segundo preceituado no inciso II do artigo 25 da Lei de Benefcios da Previdncia Social.</p><p>No se exige a qualidade de segurado como requisito para este benefcio, por fora do artigo 3 da Lei 10.666/2003.</p>',
