                            texto: '<p>&nbsp;</p><p><strong>MRITO</strong></p><p>O Auxlio Por Incapacidade Temporria Acidentrio  um benefcio por incapacidade devido ao segurado que, por sofrer um acidente de trabalho, estiver incapacitado para o trabalho ou por suas atividades habituais por mais de 15 dias, conforme normatiza o Art. 71 do Decreto n 3.048/99:</p><p><i>Art. 71.&nbsp; O auxlio por incapacidade temporria ser devido ao segurado que, uma vez cumprido, quando for o caso, o perodo de carncia exigido, ficar incapacitado para o seu trabalho ou para a sua atividade habitual por mais de quinze dias consecutivos, conforme definido em avaliao mdico-pericial.</i></p><p>Portanto, h trs requisitos a serem cumpridos: a) que o requerente seja <strong>segurado</strong>; b) que haja <strong>incapacidade</strong> laboral ou para as atividades habituais; c) que haja um acidente decorrente do trabalho realizado.</p>',
