                            texto: '<p>&nbsp;</p><p>Para a comprovao da atividade rural ou pesqueira do segurado especial, deve ser observada a seguinte normatizao pelo Decreto 3.048/99:</p><p><i>Art. 19-D. O Ministrio da Economia manter sistema de cadastro dos segurados especiais no CNIS, observado o disposto nos  7 e  8 do art. 18, e poder firmar acordo de cooperao com o Ministrio da Agricultura, Pecuria e Abastecimento e com outros rgos da administrao pblica federal, estadual, distrital e municipal para a manuteno e a gesto do sistema de cadastro.</i><br>(...)<br><i> 10. <strong>Para o perodo anterior a 1 de janeiro de 2023, o segurado especial comprovar o exerccio da atividade rural por meio de autodeclarao ratificada por entidades pblicas credenciadas</strong>, nos termos do disposto no art. 13 da Lei n 12.188, de 11 de janeiro de 2010, e por outros rgos pblicos, observado o seguinte:&nbsp;</i><br><i>I - <strong>a autodeclarao ser feita por meio do preenchimento de formulrios que sero disponibilizados pelo INSS</strong>;</i><br><i>II - a ratificao da autodeclarao ser realizada por meio de informaes obtidas das bases de dados da Secretaria de Agricultura Familiar e Cooperativismo do Ministrio da Agricultura, Pecuria e Abastecimento e de outras bases de dados a que o INSS tiver acesso;</i><br><i>III - as <strong>informaes obtidas por meio de consultas s bases de dados governamentais que forem consideradas insuficientes</strong> para o reconhecimento do exerccio da atividade rural alegada <strong>podero ser complementadas por prova documental contempornea ao perodo informado.</strong></i> (grifo nosso)</p><p>Observando o texto regulamentador, verifica-se que basta a apresentao da autodeclarao condizente com a atividade de uma pessoa produtora rural ou pescadora artesanal, carreada com documentos que sirvam como instrumentos ratificadores.&nbsp;</p><p>Todavia, o INSS  responsvel por determinar os campos importantes que devem ser preenchidos pelo segurado a fim de colher os elementos bsicos para se comprovar a condio rurcola ou pesqueira (Dec. 3.048/99, Art. 19-D,  10, I) e, caso o recorrente omita tais informaes, no  possvel constatar a filiao como segurado especial.</p>',
