                            texto: '<p>&nbsp;</p><p><strong>COMPROVAO DA ATIVIDADE RURCOLA OU PESQUEIRA DO SEGURADO ESPECIAL</strong></p><p>O Decreto n 3.048/99  taxativo, claro e especfico sobre as situaes que descaracterizam o produtor rural da condio de segurado especial, <i>in verbis</i>:</p><p> <i>Art. 9 So segurados obrigatrios da previdncia social as seguintes pessoas fsicas</i><br><i>(...)</i><br><i>VII - como segurado especial: a pessoa fsica residente no imvel rural ou em aglomerado urbano ou rural prximo que, individualmente ou em regime de economia familiar, ainda que com o auxlio eventual de terceiros, na condio de:</i><br><i>a) <strong>produtor</strong>, seja ele proprietrio, usufruturio, possuidor, assentado, parceiro ou meeiro outorgados, comodatrio ou arrendatrio rurais, que explore atividade:</i><br><i>1. agropecuria em rea contnua ou no de at quatro mdulos fiscais; ou&nbsp;</i><br><i>2. de seringueiro ou extrativista vegetal na coleta e extrao, de modo sustentvel, de recursos naturais renovveis, e faa dessas atividades o principal meio de vida;&nbsp;</i><br><i>b) <strong>pescador artesanal</strong> ou a este assemelhado, que faa da pesca profisso habitual ou principal meio de vida; e&nbsp;</i><br><i>c) <strong>cnjuge ou companheiro, bem como filho maior de dezesseis anos de idade</strong> ou a este equiparado, do segurado de que tratam as alneas a e b deste inciso, que, comprovadamente, tenham participao ativa nas atividades rurais ou pesqueiras artesanais, respectivamente, do grupo familiar.</i><br><i>()</i><br><i> 8 No  segurado especial o membro de grupo familiar que <strong>possuir outra fonte de rendimento</strong>, exceto se decorrente de:&nbsp;</i><br><i>I - benefcio de penso por morte, auxlio-acidente ou auxlio-recluso, cujo valor no supere o do menor benefcio da previdncia social;</i><br><i>I-A - benefcio concedido ao segurado qualificado como segurado especial, independentemente do valor;</i><br><i>II - benefcio previdencirio pela participao em plano de previdncia complementar institudo nos termos do inciso III do  18 deste artigo;</i><br><i>III - exerccio de atividade remunerada em perodo no superior a cento e vinte dias, corridos ou intercalados, no ano civil, observado o disposto no  22;</i><br><i>IV - exerccio de mandato eletivo de dirigente sindical de organizao da categoria de trabalhadores rurais</i><br><i>V - exerccio de mandato de vereador do municpio onde desenvolve a atividade rural, ou de dirigente de cooperativa rural constituda exclusivamente por segurados especiais, observado o disposto no  22 deste artigo;</i><br><i>VI - parceria ou meao outorgada na forma e condies estabelecidas no inciso I do  18 deste artigo;</i><br><i>VII - atividade artesanal desenvolvida com matria-prima produzida pelo respectivo grupo familiar, podendo ser utilizada matria-prima de outra origem, desde que, nesse caso, a renda mensal obtida na atividade no exceda ao menor benefcio de prestao continuada da previdncia social; e&nbsp;</i><br><i>VIII - atividade artstica, desde que em valor mensal inferior ao menor benefcio de prestao continuada da previdncia social.</i><br><i>()</i><br><i> 23. O segurado especial <strong>fica excludo dessa categoria</strong>:&nbsp;</i><br><i>I - a contar do primeiro dia do ms em que:&nbsp;</i><br><i>a) deixar de satisfazer as condies estabelecidas no inciso VII do caput deste artigo, sem prejuzo do disposto no art. 13, ou exceder qualquer dos limites estabelecidos no inciso I do  18 deste artigo;&nbsp;</i><br><i>b) se enquadrar em qualquer outra categoria de segurado obrigatrio do Regulamento da Previdncia Social, exceto nas hipteses previstas nos incisos III, V, VII e VIII do  8 e no inciso VIII do  18, sem prejuzo do disposto no art. 13;&nbsp;</i><br><i>c) se tornar segurado obrigatrio de outro regime previdencirio; ou</i><br><i>d) na hiptese de descumprimento do disposto no inciso VIII do  18:</i><br><i>1. participar de sociedade empresria ou de sociedade simples; ou&nbsp;</i><br><i>2. atuar como empresrio individual ou como titular de empresa individual de responsabilidade limitada; ou&nbsp;</i><br><i>II - a contar do primeiro dia do ms subseqente ao da ocorrncia, quando o grupo familiar a que pertence exceder o limite de:&nbsp;</i><br><i>a) utilizao de trabalhadores nos termos do  21 deste artigo;</i><br><i>b) dias em atividade remunerada estabelecidos no inciso III do  8 deste artigo; e&nbsp;</i><br><i>c) dias de hospedagem a que se refere o inciso II do  18 deste artigo. (grifos nossos)</i></p><p>Observando a norma regulamentadora, verifica-se que, para excluir o pescador artesanal ou o produtor rural da condio de segurado especial,  necessrio ficar comprovado qualquer um dos elementos constantes nos pargrafos 8 ou 23 do Art. 9 do Decreto Regulamentador. <strong>Qualquer outro motivo, que no esteja previsto na norma, caracteriza-se ato subjetivo, devendo ser declarado ilegal.</strong></p>',
