                            texto: '<p>O recurso  tempestivo e esto presentes os demais pressupostos de admissibilidade do recurso. O interessado busca a reforma da deciso mediante reconhecimento do exerccio de atividade rural no perodo de ______ a ______ , como ______ , todavia , em que pese tenha informado criar ______ , ______ &nbsp;e ______ , para subsistncia do grupo familiar, ele comprovou possuir ______ , o que o desqualifica como segurado especial, mas como trabalhador rural, na condio de contribuinte individual, nos moldes do art. 11, V, alnea a da Lei 8.213/91: Art.11 [...] V - como contribuinte individual: (Redao dada pela Lei n 9.876, de 26.11.99) a) a pessoa fsica, proprietria ou no, que explora atividade agropecuria, a qualquer ttulo, em carter permanente ou temporrio, em rea superior a 4 (quatro) mdulos fiscais; ou, quando em rea igual ou inferior a 4 (quatro) mdulos fiscais ou atividade pesqueira, com auxlio de empregados ou por intermdio de prepostos; ou ainda nas hipteses dos  9o e 10 deste artigo; (Redao dada pela Lei n 11.718, de 2008) Art. 11. So segurados obrigatrios da Previdncia Social as seguintes pessoas fsicas: (Redao dada pela Lei n 8.647, de 1993)[...] VII  como segurado especial: a pessoa fsica residente no imvel rural ou em aglomerado urbano ou rural prximo a ele que, individualmente ou em regime de economia familiar, ainda que com o auxlio eventual de terceiros, na condio de: (Redao dada pela Lei n 11.718, de 2008) a) produtor, seja proprietrio, usufruturio, possuidor, assentado, parceiro ou meeiro outorgados, comodatrio ou arrendatrio rurais, que explore atividade: (Includo pela Lei n 11.718, de 2008) 1. agropecuria em rea de at 4 (quatro) mdulos fiscais; (Includo pela Lei n 11.718, de 2008) 2. de seringueiro ou extrativista vegetal que exera suas atividades nos termos do inciso XII do caput do art. 2 da Lei n 9.985, de 18 de julho de 2000, e faa dessas atividades o principal meio de vida; (Includo pela Lei n 11.718, de 2008) b) pescador artesanal ou a este assemelhado que faa da pesca profisso habitual ou principal meio de vida; e &nbsp;(Includo pela Lei n 11.718, de 2008) c) cnjuge ou companheiro, bem como filho maior de 16 (dezesseis) anos de idade ou a este equiparado, do segurado de que tratam as alneas a e b deste inciso, que, comprovadamente, trabalhem com o grupo familiar respectivo. (Includo pela Lei n 11.718, de 2008)  10. O segurado especial fica excludo dessa categoria: (Includo pela Lei n 11.718, de 2008) [...] I  a contar do primeiro dia do ms em que: (Includo pela Lei n 11.718, de 2008) [...] b) enquadrar-se em qualquer outra categoria de segurado obrigatrio do Regime Geral de Previdncia Social, ressalvado o disposto nos incisos III, V, VII e VIII do  9o e no  12, sem prejuzo do disposto no art. 15; [...] &nbsp;Alm disso, referido entendimento encontra-se em harmonia com os termos da jurisprudncia transcrita a seguir: EMENTA: PREVIDENCIRIO. APOSENTADORIA RURAL POR IDADE. REQUISITOS LEGAIS. PRODUO AGRCOLA ELEVADA. UTILIZAO DE MAQUINRIO. DESCARACTERIZAO DO REGIME DE ECONOMIA FAMILIAR. [...]A produo agrcola elevada associada  utilizao de maquinrio evidencia a chamada agricultura empresarial, afastando a condio de segurado especial e descaracterizando o regime de economia familiar. (TRF4 5029495-06.2015.404.9999, Quinta Turma, Relator p/ Acrdo Luiz Antonio Bonat, juntado aos autos em 30/03/2016). Ementa PREVIDENCIRIO. TRABALHADOR RURAL. REGIME DE ECONOMIA FAMILIAR. PRODUO ALM DO INDISPENSVEL  SUBSISTNCIA. 1. A teor do que prev a legislao previdenciria, o regime de economia familiar se caracteriza pela atividade realizada pelos membros da famlia, indispensvel  prpria subsistncia, sem o auxlio de empregados 2. Comprovado que a produo supera em muito o indispensvel  subsistncia do grupo familiar, o proprietrio no se enquadra como pequeno produtor rural em regime de economia familiar para a prpria subsistncia. (TRF-4, 5 Turma, publicado em 29/06/04, DJ, p. 322).Assim, no tendo ficado comprovada a condio de segurado especial, na forma do art.11, VII, alnea a da Lei 8.213/91, no entendo devido o benefcio de aposentadoria por idade rural, na forma do art. 26, III, 39, I c/c 48, 2 da Lei 8.213/91:<br>Lei 8.213/91<br>Art. 26. Independe de carncia a concesso das seguintes prestaes:<br>[...]<br>III - os benefcios concedidos na forma do inciso I do art. 39, aos segurados especiais referidos no inciso VII do art. 11 desta Lei;<br>[...]<br>Art. 39. &nbsp;Para os segurados especiais, referidos no inciso VII do caput do art. 11 desta Lei, fica garantida a concesso: &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(Redao dada pela Lei n 13.846, de 2019)<br>I - de aposentadoria por idade ou por invalidez, de auxlio-doena, de auxlio-recluso ou de penso, no valor de 1 (um) salrio mnimo, e de auxlio-acidente, conforme disposto no art. 86 desta Lei, desde que comprovem o exerccio de atividade rural, ainda que de forma descontnua, no perodo imediatamente anterior ao requerimento do benefcio, igual ao nmero de meses correspondentes  carncia do benefcio requerido, observado o disposto nos arts. 38-A e 38-B desta Lei; ou &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; (Redao dada pela Lei n 13.846, de 2019)<br>[...]<br>Art. 48 [...] &nbsp; &nbsp; &nbsp; &nbsp; 2o &nbsp;Para os efeitos do disposto no  1o deste artigo, o trabalhador rural deve comprovar o efetivo exerccio de atividade rural, ainda que de forma descontnua, no perodo imediatamente anterior ao requerimento do benefcio, por tempo igual ao nmero de meses de contribuio correspondente  carncia do benefcio pretendido, computado o perodo &nbsp;a que se referem os incisos III a VIII do  9o do art. 11 desta Lei. &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(Redao dada pela Lei n 11,718, de 2008)<br>Pelo exposto, conheo do recurso e voto no sentido de NEGAR-LHE PROVIMENTO.&nbsp;<br>&nbsp;</p>',
